@import url(https://fonts.googleapis.com/css2?family=Manrope&display=swap );
@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap );
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
::before,
::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0 ;
}

:root {
  --primary-color: #fff !important;
  --secondary-color: #8c30f5 !important;
}

body {
  font-family: "Manrope", sans-serif !important;
}
a{
    font-size: larger;
}

header {
  background: #fff !important;
}

button {
  border-radius: 5px !important;
  font-family: "Rubik", sans-serif !important;
  font-weight: bold !important;
}

a {
  text-decoration: none!important;
  border-radius: 5px !important;
  cursor: pointer !important;
}

header {
  border-bottom: 1px solid black !important;
}

#usersname111 {
  color: #8c30f5 !important;
  text-align: center !important;
}

.logo-nav {
    width: 138px !important;
  }
  
 .navbar {
font-weight: 700 !important;
  }
  
  .navbar .navbar-brand {
    color: #8c30f5 !important;
  }
  
  .navbar .navbar-brand:hover {
    color: #560ea8 !important;
  }
  
  .right-nav {
    display: flex !important;
    align-items: center !important;
    padding: 0px !important;
  }
  
  .right-nav .btn {
    font-size: 15px !important;
    border-radius: 8px !important;
    width: 80px !important;
  }
  
  .right-nav .btn:first-child {
    background: #f1e4ff !important;
    color: #8c30f5 !important;
  }
  
  .right-nav .btn:first-child:hover {
    background: #c0a7db !important;
    color: #fff !important;
  }
  
  .right-nav .btn:last-child {
    background: #8c30f5 !important;
    color: #fff !important;
  }
  
  .right-nav .btn:last-child:hover {
    background: #5f1eaa !important;
  }
  


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
a {
  color: red;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

